.container-r{
    width: 100%;
    height:75vh;
    position: relative;
}

.form-box h1{
    font-size: 30px;
    margin-bottom: 60px;
    color: #1662a1;
    position: relative;
    line-height: 50px;
}

.form-box h1::after{
    content: '';
    width: 30px;
    height: 4px;
    border-radius: 3px;
    background: #1662a1;
    position: absolute;
    bottom: -12px;
    left: 50%;
    transform: translateX(-50%);
}


.button-view{
    flex-basis: 48%;
    background: #1662a1;
    color: #fff;
    height: 40px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
    text-align: center;
    line-height: 50%;
}

.form-box-c{
    position: absolute;
    left: 5%;
    background: #fff;
    padding: 20px 0;
    text-align: center;
    overflow: scroll;
    align-items: center;
}

.field-button{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.field-button button{
    flex-basis: 48%;
    background: #1662a1;
    color: #fff;
    height: 40px;
    border-radius: px;
    border: 0;
    outline: 0;
    cursor: pointer;
    transition: background 1s;
    line-height: 50%;
}

.input-field-c{
    background: #eaeaea;
    border-radius: 3px;
    display: flex;
    align-items: center;
    height: 50px;
    width: 100%;
}

input{
    background: transparent;
    border: 0;
    outline: 0;
    width: 100%;
}

.input-field-c i{
    margin-left: 15px;
    color: #999;
}

form p{
    text-align: left;
    font-size: 13px;
}

.input-group-2-c{
    display: flex;
}

.details-p{
    padding: 80px 0;
    width: 1000px;
}

.title-p h1{
    font-size: 50px;
    font-weight: bold;
}

.description-p p{
    font-size: 28px;
}

.button-p{
    text-align: center;
    padding: 10px 0;
    display: flex;
    flex-direction: row;
}

.button-p button{
    background: #1662a1;
    color: #fff;
    height: 40px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
    text-align: center;
    line-height: 50%;
    width: 450px;
}