.navbar-h{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 5px;
    padding-left: 50px;
    padding-right: 15px;
    box-shadow: 0 2px 8px 0 rgba(0,0,0,0.2);
}

.logo-h{
    cursor: pointer;
}

.navbar-h ul li{
    list-style: none;
    display: inline-block;
    margin: 0 20px;
    position: relative;
}

.navbar-h ul li a{
    color: black;
    font-weight: bold;
    text-decoration: none;
}

.navbar-h ul li::after{
    content: '';
    height: 3px;
    width: 0;
    color: #1662a1;
    position: absolute;
    left: 0;
    bottom: 0;
    transition: 0.5s;
}

.navbar-h ul li a:hover{
    color: #1662a1;
}

.register{
    border: none;
    background: #1662a1;
    padding: 12px 30px;
    border-radius: 12px;
    color: white;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
}

.heading{
    color: #1662a1;
    position: relative;
    background: white;
}