.container{
    min-height: 50px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 20px;
}

.content-section{
    display: flex;
    justify-content: center;
    align-items: center;
}

.content-section .card{
    box-shadow: 0 2px 8px 0 rgba(0,0,0,0.2);
}

.content-section .card h2{
    margin: 15px 0;
    font-size: 28px;
    color: #1662a1;
    font-family: sans-serif;
    padding: 0 10px;
}

.content-section .card p{
    font-size: 16px;
    line-height: 1.5;
    padding: 0 10px;
    margin-bottom: 20px;
    font-family: sans-serif;
}

.content-section .card:hover{
    scale: 1.02;
    cursor: pointer;
}

.content-section .card strong{
    color: rgb(14, 122, 113);
    padding: 0 10px;
}