.container-d{
    width: 100%;
    height: 60vh;
    position: relative;
}

.form-box-d{
    width: 90%;
    max-width: 450px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    padding: 50px 60px 70px;
    text-align: center;
}

.form-box-d h1{
    font-size: 30px;
    margin-bottom: 60px;
    color: #1662a1;
    position: relative;
    line-height: 50px;
}

.form-box-d h1::after{
    content: '';
    width: 30px;
    height: 4px;
    border-radius: 3px;
    background: #1662a1;
    position: absolute;
    bottom: -12px;
    left: 50%;
    transform: translateX(-50%);
}

.input-field-d{
    background: #eaeaea;
    margin: 15px 0;
    border-radius: 3px;
    display: flex;
    align-items: center;
}

input{
    width: 100%;
    background: transparent;
    border: 0;
    outline: 0;
    padding: 18px 15px;
}

.input-field-d i{
    margin-left: 15px;
    color: #999;
}

form p{
    text-align: left;
    font-size: 13px;
}

form p a{
    text-decoration: none;
    color: #1662a1;
}

.button-field-d{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.button-field-d button{
    flex-basis: 48%;
    background: #1662a1;
    color: #fff;
    height: 40px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
    transition: background 1s;
    line-height: 50%;
}

.delete-button{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 20px 20px;
}

.delete-button button{
    flex-basis: 48%;
    background: red;
    color: #fff;
    height: 40px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
    transition: background 1s;
    line-height: 50%;
}

