.section-a{
    align-items: center;
    padding: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    padding: 50px 60px 70px;
}

.button-a{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.button-a button{
    background: #1662a1;
    color: #fff;
    height: 40px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
    line-height: 50%;
}

.social-a i{
    padding: 20px 20px;
}

.title-a h1{
    text-decoration: none;
    font-weight: bold;
    padding: 0 300px;
    font-size: 28px;
}

.content-a h2 p{
    text-align: center;
    align-items: center;
}

.content-a h2 p{
    width: 500px;
}