.button-post{
    width: 30%;
    display: flex;
    justify-content: space-between;
    height: 10vh;
    text-align: center;
}

.button-post button{
    flex-basis: 48%;
    background: #1662a1;
    color: #fff;
    height: 40px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
    text-align: center;
    line-height: 50%;
}

.input-field{
    background: #eaeaea;
    margin: 15px 0;
    border-radius: 3px;
    display: flex;
    align-items: center;
}

input{
    width: 100%;
    background: transparent;
    border: 0;
    outline: 0;
    padding: 18px 15px;
}

textarea{
    width: 100%;
    background: transparent;
    border: 0;
    outline: 0;
    padding: 18px 15px;
    background-color: rgb(197, 201, 221)
}

h1{
    font-weight: bold;
}

.posting-form{
    padding: 50px 50px;
}