.slider{
    min-height: 250px;
    width: 100%;
    background-color: white;
    box-shadow: 0 2px 8px 0 rgba(0,0,0,0.2);
    align-items: center;
    display: flex;
}

.container-main{
    padding: 5px 30px;
    align-items: center;
}

.holder{
    color: white;
}

.searchbar{
    background: #eaeaea;
    margin: 15px 0;
    border-radius: 3px;
    display: flex;
    align-items: center;
}

.search-button{
    width: 100px;
    display: flex;
}

.search-button button{
    background: #1662a1;
    color: #fff;
    height: 60px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
    line-height: 50%;
}

.search{
    width: 100%;
    position: absolute;
    right: 1%;
    padding: 50px 60px 70px;
    text-align: center;
    display: flex;
    align-items: center;
}

footer{
    text-align: center;
}

.run-button{
    width: 100%;
}

.run-button button{
    background: #1662a1;
    color: #fff;
    height: 40px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
    line-height: 50%;
    width: 100%;
}

.product-display{
    padding: 20px 20px;
}